@import "main";
@import url('https://fonts.googleapis.com/css?family=Noto+Sans+JP|Source+Sans+Pro&display=swap&subset=cyrillic,cyrillic-ext,greek,greek-ext,japanese,latin-ext,vietnamese');
body{
    margin:0; padding:0;
    color: black;
    background-color: #fff;
  }

ul {
	list-style-type: none;
	margin: 0;
	padding: 0;
	overflow: hidden;
    background-color: #1f98c8;
}
li {
	float: left;
	border-right: 1px solid #bbbbbb;
}
li a {
	display: block;
	color: white;
	text-align: center;
	padding: 14px 16px;
    text-decoration: none;
}

footer{
    width: 100%;
    height: 100px;
    background-color: #757575;
    color: white;
    text-align: center;
}
ul.footer-menu li {
    display: inline;
}